	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import jiankangdangan from '@/views/jiankangdangan/list'
	import chat from '@/views/chat/list'
	import yonghu from '@/views/yonghu/list'
	import yuyueyisheng from '@/views/yuyueyisheng/list'
	import discussyuyueyisheng from '@/views/discussyuyueyisheng/list'
	import yuyuedingdan from '@/views/yuyuedingdan/list'
	import yisheng from '@/views/yisheng/list'
	import config from '@/views/config/list'
	import fuwuleixing from '@/views/fuwuleixing/list'
	import users from '@/views/users/list'
	import yishengRegister from '@/views/yisheng/register'
	import yishengCenter from '@/views/yisheng/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/yishengCenter',
			name: '',
			component: yishengCenter
		}
		,{
			path: '/jiankangdangan',
			name: '',
			component: jiankangdangan
		}
		,{
			path: '/chat',
			name: '',
			component: chat
		}
		,{
			path: '/yonghu',
			name: '',
			component: yonghu
		}
		,{
			path: '/yuyueyisheng',
			name: '',
			component: yuyueyisheng
		}
		,{
			path: '/discussyuyueyisheng',
			name: '',
			component: discussyuyueyisheng
		}
		,{
			path: '/yuyuedingdan',
			name: '',
			component: yuyuedingdan
		}
		,{
			path: '/yisheng',
			name: '',
			component: yisheng
		}
		,{
			path: '/config',
			name: '',
			component: config
		}
		,{
			path: '/fuwuleixing',
			name: '',
			component: fuwuleixing
		}
		,{
			path: '/users',
			name: '',
			component: users
		}
		]
	},
	{
		path: '/yishengRegister',
		name: '',
		component: yishengRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
